import React from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { CLASS_NAME, GrupoCentroCusto } from '../../../../models/dtos/ger/grupoCentroCusto';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface GrupoCentroCustoProps extends EditViewProps<GrupoCentroCusto> {}

const GrupoCentroCustoEditView: React.FC<GrupoCentroCustoProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<GrupoCentroCusto>({ defaultValues: selectedItem, mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().nroDigitosCentroCusto <= 0) {
      erros.push('Número de dígitos de centro de custo analítico deve ser maior que 0.');
    }

    return erros;
  };

  return (
    <WindowComponent
      title="Manutenção de Grupo de Centro de Custo"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasGrupoEmpresaConfig(globalReducer.globalParameter)]}
      wikiId={1258}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 8) } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              placeholder="Preenchimento Opcional - Codificação  Automática"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
            />
          </Col>
        </Row>
        <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
          <CardBody>
            <CardTitle>Configurações</CardTitle>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { ...onlyNumber(0, 2), required: true } }}
                  id="nroDigitosCentroCusto"
                  isPrependLabel={true}
                  label="Nº de digitos para o centro de custo analítico "
                  type={InputType.NUMBER}
                />
              </Col>
            </Row>
          </CardBody>
        </Card>
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoCentroCustoEditView;
